<p>Refer to the official document: <a href="https://cesium.com/docs/cesiumjs-ref-doc/ArcGISTiledElevationTerrainProvider.html">ArcGISTiledElevationTerrainProvider</a></p>
<h2>Events</h2>
<table>
<thead>
<tr>
<th>name</th>
<th>parameter</th>
<th>description</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>Triggers when the component is ready. It returns a core class of Cesium, a viewer instance, and the cesiumObject.</td>
</tr>
<tr>
<td>errorEvent</td>
<td>TileProviderError</td>
<td>Triggers when the terrain provider encounters an asynchronous error.</td>
</tr>
